import React, { useState } from "react";
import styles from "../styles/Chat.module.css";
import Capsule from "../components/Capsule";
import Message from "../components/Message";
import UserMessage from "../components/UserMessage";
import Send from "../asset/send-button.svg";
const content = [
  {
    q: "I am feeling depressed",
    a: [
      "okay,can u go into detail",
      "How can I help you?",
      "It's normal to feel like this.",
    ],
  },
];
const Chat = (props) => {
  const [number, setNumber] = useState(0);
  const [messages, setMessages] = useState([]);
  const [selected, setSelected] = useState([false, false, false]);
  const optionClicked = (index) => {
    const data = [false, false, false];
    data[index] = true;
    setSelected(data);
  };

  const sendHandler = () => {
    const index = selected.findIndex((ele) => ele === true);
    console.log(index);
    if (index !== -1) {
      const data = messages.push(content[number].a[0]);
      console.log({ data });
      // setMessages(data);
    }
  };
  return (
    <div>
      <div className={styles.wrapper}>
        <div className={styles.messages}>
          {messages.map((ele) => (
            <Message content={ele} />
          ))}
        </div>
        <div className={styles.optionsBox}>
          {content[number].a.map((ele, index) => (
            <Capsule
              clicked={() => optionClicked(index)}
              selected={selected[index]}
            >
              {ele}
            </Capsule>
          ))}
          <div className={styles.space}></div>
        </div>
        <button className={styles.button} onClick={sendHandler}>
          <img src={Send} alt="send" className={styles.image} />
        </button>
      </div>
    </div>
  );
};

export default Chat;
